import { Injectable } from '@angular/core';
import { ToastController, ToastOptions } from '@ionic/angular';
import { ToastHelper } from './class/ultisInterface';

@Injectable({
  providedIn: 'root'
})
export class ToastService {
  toast!: HTMLIonToastElement;
  constructor(
    public toastController: ToastController
  ) { }
  async presentToast(args: ToastOptions) {
    if (this.toast) this.toast.dismiss();
    args.duration = args.duration ? args.duration : 1000;
    args.cssClass = args.cssClass ? args.cssClass : "toast-service";
    args.color = args.color ? args.color : "secondary";
    args.keyboardClose = true
    args.buttons = args.buttons ? args.buttons : [{
      side: 'end',
      role: 'close',
      icon: 'close'
    }]
    this.toast = await this.toastController.create(args);
    this.toast.present();
  }

  async helper(args: ToastHelper) {
    const toast = await this.toastController.create({
      header: args?.header || 'Giải thích',
      message: args?.msg || '',
      position: 'top',
      color: "danger",
      cssClass: "toast-helper",
      buttons: [
        {
          side: 'start',
          icon: 'help-circle-outline'
        }, {
          text: 'Thêm',
          role: 'openlink',
          handler: () => {
            window.open(args.link, "_blank")
          }
        }, {
          text: 'OK',
          role: 'cancel'
        },
      ]
    });
    await toast.present();
  }

  /*
  Thông báo khi tạo Ankipakage ở trang hồ sơ 
   */
  async infoCreatingAnki(args: ToastOptions) {
    if (this.toast) this.toast.dismiss();
    args.duration = args.duration ? args.duration : 1000;
    args.cssClass = args.cssClass ? args.cssClass : "toast-service";
    args.color = args.color ? args.color : "secondary";
    args.keyboardClose = true
    args.buttons = args.buttons ? args.buttons : [{
      side: 'end',
      role: 'close',
      icon: 'close'
    }]
    this.toast = await this.toastController.create(args);
    this.toast.present();
  }

}
