import { Component, OnInit } from '@angular/core';
import { ToastService } from '../service/toast-service.service';

declare const navigator: any
@Component({
  selector: 'app-dong-gop',
  templateUrl: './dong-gop.page.html',
  styleUrls: ['./dong-gop.page.scss'],
})
export class DongGopPage implements OnInit {
  TKNH = 101867846041
  TKMOMO = "0394446896"
  donggopkimcuong = [{ name: "Tên hiển thị", tengoithuong: "Tên gọi thường", loinhan: "Lời nhắn" }]
  donggopruby = [{ name: "Tên hiển thị", tengoithuong: "Tên gọi thường", loinhan: "Lời nhắn" }]
  donggopvang = [{ name: "Tên hiển thị", tengoithuong: "Tên gọi thường", loinhan: "Lời nhắn" }]
  openLicense: any
  constructor(
    private toastController: ToastService
  ) { }

  ngOnInit() {
  }
  copyTKNH() {
    try {
      navigator.clipboard.writeText(this.TKNH);
      this.toastController.presentToast({ message: `Đã copy số TK ngân hàng: ${this.TKNH}` })
    } catch (error) {
      this.toastController.presentToast({ message: `KHÔNG thể số TK ngân hàng` })
    }
  }
  copyTKMOMO() {
    try {
      navigator.clipboard.writeText(this.TKMOMO);
      this.toastController.presentToast({ message: `Đã copy số TK Momo: ${this.TKMOMO}` })
    } catch (error) {
      this.toastController.presentToast({ message: `KHÔNG thể số TK Momo` })
    }
  }

}
